<template>
<div class="background">
  <!--标题-->
  <Header>
    <div slot="left" class="backIcon"><img :src="`${$cdnImageUrl}/common/back_white.png`"></div>
    <div slot="title">管理增票信息</div>
    <div slot="right" @click="$router.push('/batchDeleteInvoice')" v-if="voiceList.length">批量删除</div>
  </Header>
  <div style="padding-bottom: 100px">
      <van-search
        v-model="keyword"
        shape="round"
        show-action
        @search="onSearch"
        @clear="onCancel"
        placeholder="请输入发票抬头"
      >
        <template #action>
          <div @click="onSearch">搜索</div>
        </template>
      </van-search>
      <div v-if="!voiceList.length" class="flex-common" style="margin-top:64px;">
          <div style="font-size: 14px;color: #999;letter-spacing: 0;text-align: center;width: 100%;text-align:center;margin-top:10px;">暂无数据</div>
      </div>
      <!--增票列表-->
      <div v-if="voiceList && voiceList.length > 0" style="margin-top: 8px;">
          <div v-for="item in voiceList" :key="item.id" style="padding:0 16px;background:#ffffff;margin-bottom: 8px;" @click="setTempInvoice(item)">
              <div style="padding-top: 15px;font-size: 14px;">
                  <span :style="{color: '#000'}">{{item.invoiceTitle}}</span>
              </div>
              <div style="font-size: 12px;color: #000;margin-top: 10px; margin-bottom: 15px;">{{item.taxPayerNumber}}</div>
              <div class="flex-common" style="position:relative;height:44px;font-size: 12px;color:#666666;">
                  <div class="flex-common">
                      <img @click.stop="setDefault(item)" :src="`${$cdnImageUrl}${ item.isDefault ? '/common/checked.svg' : '/cart/icon-unselected.png'}`" style="width: 16px;height:16px;margin:0 10px 0 0;" />
                      <span>默认增票信息</span>
                  </div>
                  <div class="flex-common" @click.stop="deleteInvoice(item.id)" v-if="!item.isDefault">
                      <span class="icon-delete"></span>
                      <!-- <img :src="`${$cdnImageUrl}/img/addressEdit@2x.png`" style="width: 16px;height: 16px; margin: 0 8px 0 0 ;"> -->
                      <span>删除</span>
                  </div>
                  <div class="flex-common" @click.stop="goToChange(item.id)">
                      <span class="icon-edit"></span>
                      <!-- <img :src="`${$cdnImageUrl}/img/addressEdit@2x.png`" style="width: 16px;height: 16px; margin: 0 8px 0 0 ;"> -->
                      <span>编辑</span>
                  </div>
              </div>
          </div>
      </div>
  </div>
  <div style="width:100%;height:90px;position: fixed;bottom:0px;background: #f4f4f4;max-width: 7.5rem">
      <div @click="$router.push('/editInvoiceInfo')" style="width:91%;height:44px;margin:23px auto;background: #2979FF;border-radius: 50px;line-height:44px;text-align: center;color: #ffffff;font-size: 17px;">新建增票信息</div>
  </div>
</div>
</template>

<script>
import Header from '@/components/header'
import { memberInvoice, updateInvoice, deleteBatchInvoice } from '@/http/service/goods'
export default {
  components: {
    Header
  },
  data() {
    return {
      voiceList: [],
      keyword: ''
    }
  },
  activated() {
    this.memberInvoice()
  },
  methods: {
    onSearch() {
      if (!this.keyword) {
        this.$toast('请输入搜索内容')
        return
      }
      this.memberInvoice()
    },
    onCancel() {
      this.keyword = ''
      this.memberInvoice()
    },
    memberInvoice() {
      memberInvoice({
        k: this.keyword
      }).then(res => {
        if (res.success) {
          this.voiceList = res.data
        }
      })
    },
    setDefault(item) {
      updateInvoice({
        id: item.id,
        isDefault: item.isDefault ? 0 : 1
      }).then(res => {
        this.memberInvoice()
      })
    },
    deleteInvoice(id) {
      this.$dialog.confirm({
        showCancelButton: true,
        message: '您确认要删除吗?',
        confirmButtonText: '是',
        cancelButtonText: '否'
      }).then(res => {
        if (res === 'confirm') {
          deleteBatchInvoice({
            ids: id
          }).then(result => {
            if (result.success) {
              this.memberInvoice()
            } else {
              this.$toast.fail(result.message)
            }
          })
        }
      })
    },
    setTempInvoice(item) {
      this.$store.commit('orderInvoice/setTempInvoice', item)
      this.$router.go(-1)
    },
    goToChange(id) {
      this.$router.push({
        path: '/editInvoiceInfo',
        query: {
          id
        }
      })
    },
  }
}
</script>

