  display: flex;
  justify-content: space-between;
  align-items: center;
  .icon-edit {
    margin-right: .1rem;
    display: block;
    width: 0.32rem;
    height: 0.32rem;
    background-size: contain;
    background-position: center;
    background-image: url('data:image/png;base64,');
  }
  .icon-delete{
    margin-right: .1rem;
    display: block;
